<template>
  <div class="flex justify-center">
    <div>
      <div class="form-check">
        <input  v-model="editMealBool"  v-bind="editMealBool" type="checkbox" id="flexCheckChecked" checked="checked">
        <label class="form-check-label inline-block text-gray-800" for="flexCheckChecked">
          Modifier Prix
        </label>
      </div>
    </div>
  </div>

  <div class="flex flex-col">
    <div class="product flex font-sans border relative shadow-xl flex leading-none bg-white flex w-full  " v-for="product,key in products " v-bind:key="product">
      <div class="flex-auto p-1">
        <div class="flex flex-wrap">
          <h1 class="flex text-lg font-semibold text-slate-900">
            {{product.name}}
          </h1>
          <div class="flex-auto text-lg font-semibold text-slate-500" v-if="product.fries">
            
            Avec Frites: {{product.priceWithFries}}€, Sans : {{product.price}}€
          </div>
          <div class="flex-auto text-lg font-semibold text-slate-500" v-else>
            Prix Seul : {{product.price}}€
          </div>        </div>
      <div class="flex space-x-4 mb-6 text-sm font-medium" v-if="product.fries" >
        <div class="flex-auto flex space-x-4">
          <div class=" rounded-md shadow-sm" role="group">
            <button type="button" @click="editRepas(key,1,'countFries','priceWithFries')" class=" py-2 px-4 text-sm font-medium text-gray-900 bg-transparent rounded-l-lg border border-gray-900 hover:bg-gray-900 hover:text-white focus:z-10 focus:ring-2 focus:ring-gray-500 focus:bg-gray-900 focus:text-white dark:border-white dark:text-white dark:hover:text-white dark:hover:bg-gray-700 dark:focus:bg-gray-700">
              {{product.name}} avec Frites
            </button>
            <button type="button" class="py-2 px-4 text-sm font-medium text-gray-900 bg-transparent border-t border-b border-gray-900 hover:bg-gray-900 hover:text-white focus:z-10 focus:ring-2 focus:ring-gray-500 focus:bg-gray-900 focus:text-white dark:border-white dark:text-white dark:hover:text-white dark:hover:bg-gray-700 dark:focus:bg-gray-700">
              {{product.countFries}}
            </button>
            <button type="button" @click="editRepas(key,-1,'countFries','priceWithFries')" class="py-2 px-4 text-sm font-medium text-gray-900 bg-transparent rounded-r-md border border-gray-900 hover:bg-gray-900 hover:text-white focus:z-10 focus:ring-2 focus:ring-gray-500 focus:bg-gray-900 focus:text-white dark:border-white dark:text-white dark:hover:text-white dark:hover:bg-gray-700 dark:focus:bg-gray-700">
              -
            </button>
          </div>
        <input v-if="editMealBool" v-model="product.priceWithFries" @change="editMeal(key,product.name,product.price,product.fries,product.priceWithFries)" class="shadow appearance-none border border-red-500 rounded w-full py-2 px-3 text-gray-700 mb-3 leading-tight focus:outline-none focus:shadow-outline" type="number" placeholder="0">

        </div>
      </div>
        <div class="flex space-x-4 mb-6 text-sm font-medium">
        <div class="flex-auto flex space-x-4">
          <div class=" rounded-md shadow-sm" role="group">
            <button type="button" @click="editRepas(key,1,'count','price')" class=" py-2 px-4 text-sm font-medium text-gray-900 bg-transparent rounded-l-lg border border-gray-900 hover:bg-gray-900 hover:text-white focus:z-10 focus:ring-2 focus:ring-gray-500 focus:bg-gray-900 focus:text-white dark:border-white dark:text-white dark:hover:text-white dark:hover:bg-gray-700 dark:focus:bg-gray-700">
              {{product.name}}
            </button>
            <button type="button" class="py-2 px-4 text-sm font-medium text-gray-900 bg-transparent border-t border-b border-gray-900 hover:bg-gray-900 hover:text-white focus:z-10 focus:ring-2 focus:ring-gray-500 focus:bg-gray-900 focus:text-white dark:border-white dark:text-white dark:hover:text-white dark:hover:bg-gray-700 dark:focus:bg-gray-700">
              {{product.count}}
            </button>
            <button type="button" @click="editRepas(key,-1,'count','price')" class="py-2 px-4 text-sm font-medium text-gray-900 bg-transparent rounded-r-md border border-gray-900 hover:bg-gray-900 hover:text-white focus:z-10 focus:ring-2 focus:ring-gray-500 focus:bg-gray-900 focus:text-white dark:border-white dark:text-white dark:hover:text-white dark:hover:bg-gray-700 dark:focus:bg-gray-700">
              -
            </button>
          </div>
            <input v-if="editMealBool" v-model="product.price" @change="editMeal(key,product.name,product.price,product.fries,product.priceWithFries)" class="shadow appearance-none border border-red-500 rounded w-full py-2 px-3 text-gray-700 mb-3 leading-tight focus:outline-none focus:shadow-outline" type="number" placeholder="0">
        </div>
      </div>
      </div>

      <!-- <button @click="addMeal(0,0,0,0)">greet</button> -->
    </div>
    <div class="flex">
      <h1 class="bold text-2xl">Total : {{this.repas["total"]}}€</h1>
    </div>
    <div class="flex">
      <h1 class="bold text-2xl">Espèce donnée:</h1>
    </div>  
    <div class="flex">
      <input v-model="cashGiven" @input="amountCashToGive" class="shadow appearance-none border border-red-500 rounded w-full py-2 px-3 text-gray-700 mb-3 leading-tight focus:outline-none focus:shadow-outline" id="cash" type="number" placeholder="0">
    </div>
    <div class="flex">
      <h1 class="bold text-2xl">Espèce à rendre: {{this.cashToGive}}€</h1>
    </div>
    <div class="flew flew-row space-x-8">
      <button @click="saveRepas('totalCash')" class="bg-transparent hover:bg-green-500 text-green-700 font-semibold hover:text-white py-2 px-4 border border-green-500 hover:border-transparent rounded">
        Espèce
      </button>
      <button @click="saveRepas('totalCB')" class="bg-transparent hover:bg-blue-500 text-blue-700 font-semibold hover:text-white py-2 px-4 border border-blue-500 hover:border-transparent rounded">
        CB
      </button>
      <button @click="deleteOld" class="bg-transparent hover:bg-red-500 text-red-700 font-semibold hover:text-white py-2 px-4 border border-red-500 hover:border-transparent rounded">
        Annuler ancienne
      </button>
    </div>
  </div>

</template>

<script>

export default {
  name: 'App',
  components: {
  },
  data(){
    var clients = [];
    var products = [];
    var repas = {"meal":{},"total":0,
                "CB":false}
    var cash = {"total":0,"totalCB":0,totalCash:0}
    var cashGiven = 0;
    var cashToGive = 0;
    var cleanProduct = {};
    var editMealBool = false;
    return {products,clients,repas,cash,cashGiven,cashToGive,cleanProduct,editMealBool}
  },
  created(){
    if(localStorage.getItem("products")== null){
      var products = 
          [{
            "name":"Moules",
            "price": 4,
            "fries": true,
            "priceWithFries":7,
            "count":0,
            "countFries":0
          },{
            "name":"Sardines",
            "price": 4,
            "fries": true,
            "priceWithFries":7,
            "count":0,
            "countFries":0
          },{
            "name":"Saucisses",
            "price": 4,
            "fries": true,
            "priceWithFries":7,
            "count":0,
            "countFries":0
          },{
            "name":"Frites",
            "price": 3,
            "fries": false,
            "count":0,
            "countFries":0
          }];
      console.log("Création JSON Products");
      localStorage.setItem("products", JSON.stringify(products));
    }
    if(localStorage.getItem("clients")== null){
      var clients = 
        [];
      console.log("Création JSON Clients");
      localStorage.setItem("clients", JSON.stringify(clients));
    }
    if(localStorage.getItem("cash")== null){
      console.log("Création JSON Cash");
      localStorage.setItem("cash", JSON.stringify({"total":0,"totalCB":0,totalCash:0}));
    }  },
  beforeMount(){
    this.products = JSON.parse(localStorage.getItem("products"));
    this.clients = JSON.parse(localStorage.getItem("clients"));
    this.cash = JSON.parse(localStorage.getItem("cash"));
    this.repas["meal"] = this.products;
    this.cleanProduct = JSON.parse(JSON.stringify(this.products));
    console.log(this.products[1])
  },
  methods: {
    addMeal(name,price,withFries,priceWithFries){
      var meal = {
            "name":name,
            "price": price,
            "fries": withFries,
            "priceWithFries":priceWithFries,
            "count":0,
            "countFries":0
          }
      this.products.push(meal);
      localStorage.setItem("products", JSON.stringify(this.products));
    },
    editMeal(index,name,price,withFries,priceWithFries){
      var meal = {
            "name":name,
            "price": price,
            "fries": withFries,
            "priceWithFries":priceWithFries,
            "count":0,
            "countFries":0
          }
      this.products[index]= meal;
      localStorage.setItem("products", JSON.stringify(this.products));
      },
    deleteMeal(index){
      this.products.splice(index,1);
      localStorage.setItem("products", JSON.stringify(this.products));
    },
    newRepas(){
      this.products = JSON.parse(JSON.stringify(this.cleanProduct));
      this.repas = {"meal":this.products,"total":0,
                "CB":false};
      this.cashToGive=0;
    },
    saveRepas(cash){
      this.clients.push(this.repas)
      this.cash["total"] += this.repas["total"];
      this.cash[cash]+= this.repas["total"];
      localStorage.setItem("cash", JSON.stringify(this.cash));
      localStorage.setItem("clients", JSON.stringify(this.clients));
      this.newRepas();
    },
    editRepas(index,value,count,price){

      this.repas["meal"][index][count] += value;

      if(this.repas["meal"][index][count]<0){
        this.repas["meal"][index][count]=0;
      }
      else{
        this.repas["total"] += (this.repas["meal"][index][price] *value)
      }
      if(this.repas["total"]< 0){
        this.repas["total"] =0;
      }
    },
    amountCashToGive(){
      if(this.cashGiven >= this.repas["total"] || this.cashToGive >0){
        console.log("enough money given");
        this.cashToGive = this.cashGiven - this.repas["total"]
        this.cashToGive.toFixed(1);
        if(this.cashToGive<0){
          this.cashToGive=0;
        }
      }

    },
    deleteOld(){
      this.clients.pop();
      localStorage.setItem("clients", JSON.stringify(this.clients));
    }
  }
}
</script>

